import React from "react";
import { Link } from "react-router-dom";

export default function About() {
  const greeting = "Hello from Contact Us!";
  return (
    <div>
      <h1>{greeting}</h1>
      <ul className="">
        <li>
          <Link to="/home">Go Back</Link>
        </li>
      </ul>
    </div>
  );
}
